<template>
  <div>
    <apexchart
      v-if="loading"
      type="bar"
      width="100%"
      :height="setChartHeight()"
      :options="chartOptions"
      :series="series"
    >
    </apexchart>
  </div>
</template>
<script>
export default {
  watch: {},
  async created() {},
  async mounted() {
    await this.getFallDetection();
  },
  methods: {
    async getFallDetection() {
      try {
        // var result = await this.$http.post("events/fall_detect/all", {
        //   uid: this.$session.user.id,
        //   date: "week",
        //   format: "%m/%d",
        //   permission: this.$session.user.permission,
        // });
        var fallgraph = [];
        fallgraph.push({
            x: this.$date.changeDateSlashFormat(6),
            y: 1,
          });
        fallgraph.push({
            x: this.$date.changeDateSlashFormat(5),
            y: 1,
          });
        fallgraph.push({
            x: this.$date.changeDateSlashFormat(4),
            y: 1,
          });
        fallgraph.push({
            x: this.$date.changeDateSlashFormat(3),
            y: 1,
          });
        fallgraph.push({
            x: this.$date.changeDateSlashFormat(2),
            y: 2,
          });
        // for (var date = 5; date > 1; date--) {
        //   var computeDate = this.$date.changeDateSlashFormat(date);
        //   // var idx = result.data.findIndex((item) => {
        //   //   return item.x === computeDate;
        //   // });
        //   var min = 0
        //   var max = 3
        //   min = Math.ceil(min);
        //   max = Math.floor(max);
        //   fallgraph.push({
        //     x: computeDate,
        //     y: 0,
        //   });
          
        // }
        fallgraph.push({
            x: this.$date.changeDateSlashFormat(1),
            y: 2,
          });
        fallgraph.push({
            x: this.$date.changeDateSlashFormat(0),
            y: 2,
          });
        this.series.push({ name: "Fall", data: fallgraph });
      } catch {
        this.series.push({ name: "Fall", data: [] });
      }
      this.loading = true;
    },
    setChartHeight() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "150vh";
        case "sm":
          return 120;
        case "md":
          return "145vh";
        case "lg":
          return "160vh";
        case "xl":
          return "220vh";
      }
    },
  },
  data() {
    return {
      chartOptions: {
        colors: ["#ff0000"],
        chart: {
          type: "bar",
        },
        dataLabels: {
          enabled: true,
          style: {
            fontSize: "16px",
            fontFamily: "Helvetica, Arial, sans-serif",
            fontWeight: "bold",
          },
        },
        xaxis: {
          axisBorder: {
            show: false,
          },
          axisTicks: {
            show: false,
          },
          fill: {
            colors: ["#ff0000"],
          },
          labels: {
            style: {
              fontSize: "18px",
              fontFamily: "Helvetica, Arial, sans-serif",
              fontWeight: 400,
              cssClass: "apexcharts-xaxis-label",
            },
          },
          tooltip: {
            enabled: true,
          },
        },
        yaxis: {
          show: false,
          axisBorder: {
            show: false,
          },
          axisTicks: {
            show: false,
          },
          tickAmount: 1,
          floating: false,
        },
      },

      series: [],
      loading: false,
    };
  },
};
</script>
<style lang="scss"></style>
